import React from 'react';
import styled from 'styled-components';
import { ModalProvider } from 'styled-react-modal';

import ClientStoreProvider from '../context/ClientStore/ClientStoreProvider';
import ThemeStoreProvider from '../context/ThemeStore/ThemeStoreProvider';
import QuizStoreProvider from '../context/QuizStore/QuizStoreProvider';
import withErrorBoundary from '../hocs/withErrorBoundary';

import ConsumptionModal from './Modal/ConsumptionModal';
import StateHandler from './StateHandler';

const ModalWrapper = styled.div`
    @import url('https://fonts.googleapis.com/css?family=Rubik:300,400,500,700&subset=cyrillic,hebrew,latin-ext');
    background: #FFF3CC;
    * {
        box-sizing: border-box;
        font-family:  ${({ theme }) => (theme.font_family)};
    }
`;

function App() {
    // Check for the existence of some settings required to make
    // this library work as expected. Log an error if that's not the case
    // React.useEffect(() => {
    //     if (!document.querySelector('meta[name="viewport"]')) {
    //         console.warn('Viewport meta tag should be present for content-types to behave correctly');
    //     }
    // }, []);

    return (
        <ClientStoreProvider>
            <ThemeStoreProvider>
                <ModalWrapper>
                    <ModalProvider>
                        <ConsumptionModal onNext={e => e.preventDefault()} nextEnabled>
                            <QuizStoreProvider>
                                <StateHandler />
                            </QuizStoreProvider>
                        </ConsumptionModal>
                    </ModalProvider>
                </ModalWrapper>
            </ThemeStoreProvider>
        </ClientStoreProvider>
    );
}

export default withErrorBoundary(App, 'AppBoundary');
