import {
    ADVANCE_TO_NEXT_QUESTION,
    SCREEN_STATES,
    SELECT_ANSWER,
    UPDATE_SCREEN_STATE,
} from './QuizStoreActions';

const QuizReducer = (state, action = { type: '' }) => {
    switch (action.type) {
        case ADVANCE_TO_NEXT_QUESTION:
            state.currentQuestionIndex += 1;

            if (state.currentQuestionIndex >= state.questions.length) {
                state.screenState = SCREEN_STATES.COMPLETED;
            }
            return;

        case UPDATE_SCREEN_STATE:
            state.screenState = action.screenState;
            return;

        case SELECT_ANSWER:
            const question = state.questions.find(q => q.id === action.questionId); // eslint-disable-line
            const answerToSelect = question.answers.find(e => e.id === action.answerId); // eslint-disable-line
            answerToSelect.isSelected = true;
    }
};

export default QuizReducer;
