import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import HideComponent from '@wikia/react-common/components/HideComponent';
import StyledCountdown from '@wikia/react-common/components/StyledCountdown';

import QuestionTitle from './QuestionTitle';
import QuestionAnswers from './QuestionAnswers';
import delayedTimeout from './delayedTimeout';

const TimerPosition = styled.div`
    align-items: center;
    display: flex;
    flex-direction: column;
    left: 50%;
    position: absolute;
    text-align: center;
    top: 18px;
    transform: translate(-50%, 0);
`;

const TimeIsAlmostUp = styled.span`
    color: ${({ theme }) => theme.color.warning};
    font-size: ${({ theme }) => theme.font_size.s};
`;

const TimeIsUp = styled.span`
    color: ${({ theme }) => theme.color.alert};
    font-size: ${({ theme }) => theme.font_size.s};
`;

const TIME_IS_UP = Object.freeze({
    NO: 'no',
    ALMOST: 'almost',
    YES: 'yes',
});

function AnswersScreen({ onAnswerTimeout, onAnswer }) {
    const [timeIsUp, setTimeIsUp] = React.useState(TIME_IS_UP.NO);

    const checkTick = (remainingTime) => {
        if (remainingTime < 6 && remainingTime !== 0) {
            setTimeIsUp(TIME_IS_UP.ALMOST);
        }

        if (remainingTime === 0) {
            setTimeIsUp(TIME_IS_UP.YES);
            delayedTimeout(onAnswerTimeout);
        }
    };

    return (
        <React.Fragment>
            <TimerPosition>
                <StyledCountdown onTick={checkTick} />
                <HideComponent hide={timeIsUp !== TIME_IS_UP.ALMOST}>
                    <TimeIsAlmostUp>Time is running out!</TimeIsAlmostUp>
                </HideComponent>
                <HideComponent hide={timeIsUp !== TIME_IS_UP.YES}>
                    <TimeIsUp>Time ran out! This question will be marked as wrong.</TimeIsUp>
                </HideComponent>
            </TimerPosition>
            <QuestionTitle />
            <QuestionAnswers onAnswer={onAnswer} />
        </React.Fragment>
    );
}

AnswersScreen.propTypes = {
    onAnswer: PropTypes.func.isRequired,
    onAnswerTimeout: PropTypes.func.isRequired,
};

export default AnswersScreen;
