import React from 'react';
import PropTypes from 'prop-types';

import { useAppReducer } from '../../hooks/useAppReducer';

import QuizStore from './QuizStore';
import initialState from './initialState';
import QuizStoreReducer from './QuizStoreReducer';

function QuizStoreProvider(props) {
    const [state, dispatch] = useAppReducer(QuizStoreReducer, initialState);
    const value = { state, dispatch };

    return <QuizStore.Provider value={value}>{props.children}</QuizStore.Provider>;
}

QuizStoreProvider.propTypes = {
    children: PropTypes.node.isRequired,
};

export default QuizStoreProvider;
