import React from 'react';

import QuizStore from '../context/QuizStore/QuizStore';

import { SCREEN_STATES } from '../context/QuizStore/QuizStoreActions';

import TitleScreen from './TitleScreen';
import Question from './Question';

function StateHandler() {
    const { state } = React.useContext(QuizStore);

    if (state.screenState === SCREEN_STATES.TITLE_SCREEN) {
        return <TitleScreen />;
    }

    if (state.screenState === SCREEN_STATES.IN_PROGRESS) {
        return <Question key={state.currentQuestionIndex} />;
    }

    if (state.screenState === SCREEN_STATES.COMPLETED) {
        return <div> Completed Quiz </div>;
    }

    return <div>Unknown State</div>;
}

export default StateHandler;
