import React from 'react';
import styled from 'styled-components';
import IconClock from '@wikia/react-common/icons/IconClockSmall';
import Avatar from '@wikia/react-common/components/StyledAvatar';

import QuizStore from '../context/QuizStore/QuizStore';

import { setStateToInProgress } from '../context/QuizStore/QuizStoreActions';

import QuizCountDown from './QuizCountdown';

const TitleScreenHeading = styled.div`
    color: ${({ theme }) => theme.color.white};
    font-size: ${({ theme }) => theme.font_size.xxxxl};
    line-height: 1.15;
    margin-top: 66px;
    padding-left: 30px;
    padding-right: 30px;
    text-align: center;
    
    @media ${({ theme }) => theme.media.small_down} {
        font-size: ${({ theme }) => theme.font_size.xxl};
        margin-top: 66px;
        padding-left: 12px;
        padding-right: 12px;    
    }
`;

const TitleScreenBackground = styled.div`
`;

const TopRow = styled.div`
    display: flex;
    justify-content: space-between;
`;

const Creator = styled.div`
    align-self: flex-start;
    display: flex;
    // align-items: center;
    
    @media ${({ theme }) => theme.media.small_down} {
        font-size: ${({ theme }) => theme.font_size.s};
    }
`;

const Questions = styled.div`
    align-self: flex-end;
    font-size: ${({ theme }) => theme.font_size.base};
    
    @media ${({ theme }) => theme.media.small_down} {
        font-size: ${({ theme }) => theme.font_size.s};
    }

    
`;

const Divider = styled.div`
    background: #fff;
    height: 2px;
    margin-left: 30px;
    margin-right: 30px;
    margin-top: 44px;
    
    @media ${({ theme }) => theme.media.small_down} {
        height: 1px;
        opacity: 0.5;
    }
`;

const QuizHelp = styled.div`
    font-size: ${({ theme }) => theme.font_size.base};
    font-weight: ${({ theme }) => theme.font_weight.medium};
    margin-top: 25px;
    text-align: center;
`;

const QuizHelpDescription = styled.div`
    font-size: ${({ theme }) => theme.font_size.base};
    font-weight: ${({ theme }) => theme.font_weight.light};
    line-height: 1.5;
    margin-left: 70px;
    margin-right: 70px;
    margin-top: 12px;
    text-align: center;
      
    @media ${({ theme }) => theme.media.small_down} {
        margin-left: 6px;
        margin-right: 6px;
    }

`;

const Clock = styled(IconClock)`
    padding-right: 6px;
    vertical-align: text-top;
`;

const StyledAvatar = styled(Avatar)`
    margin-right: 3px;
    padding-right: 3px;
`;

const UserName = styled.div`
    align-self: center;
    margin-top: 8px;
    padding-left: 6px;
`;

function TitleScreen() {
    const { state, dispatch } = React.useContext(QuizStore);

    return (
        <TitleScreenBackground>
            <TopRow>
                <Creator user={state.author}>
                    <StyledAvatar size={30} />
                    <UserName>{state.author}</UserName>
                </Creator>
                <Questions>{state.questions.length} Questions </Questions>
            </TopRow>
            <TitleScreenHeading>{state.title}</TitleScreenHeading>
            <Divider />
            <QuizHelp>
                <Clock height={18} fill="white" />
                This quiz is timed!
            </QuizHelp>
            <QuizHelpDescription>
                You’ll have 10 seconds to complete each question. If you don’t complete a question, it’s counted as a wrong answer. Good luck! The quiz starts in….
            </QuizHelpDescription>
            <QuizCountDown onComplete={() => dispatch(setStateToInProgress())} />
        </TitleScreenBackground>
    );
}

export default TitleScreen;
