import React from 'react';
import styled from 'styled-components';

import { ONE_LINE_ELLIPSIS } from '../../utils/cssSnippets';
import QuizStore from '../../context/QuizStore/QuizStore';

const Wrapper = styled.div`
    color: ${({ theme }) => theme.color.white};
    font-size: ${({ theme }) => theme.font_size.base};
    margin: 0 24px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
`;

const Title = styled.div`
    flex: 1;
    font-weight: ${({ theme }) => theme.font_weight.bold};
    letter-spacing: 0.3px;
    ${ONE_LINE_ELLIPSIS}
`;

const Index = styled.div`
    margin-left: 24px;
`;


function QuestionHeader() {
    const { state } = React.useContext(QuizStore);

    return (
        <Wrapper>
            <Title>{state.title}</Title>
            <Index>{state.currentQuestionIndex + 1} / {state.questions.length}</Index>
        </Wrapper>
    );
}

export default QuestionHeader;
