import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import { CENTER_WITH_FLEX } from '../../utils/cssSnippets';
import QuizStore from '../../context/QuizStore/QuizStore';

const Answer = styled.div`
    background: ${({ incorrect, theme }) => (incorrect ? theme.color.alert : theme.color.white)};
    border-radius: 3px;
    border: 0;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, .3);
    color: ${({ incorrect, theme }) => (incorrect ? theme.color.white : theme.color.black)};
    font-family: inherit;
    font-size: ${({ theme }) => theme.font_size.base};
    height: 55px;
    margin-top: 18px;
    width: 100%;
    ${CENTER_WITH_FLEX}
`;

const AnswerButton = styled(props => <Answer as="button" {...props} />)`
    cursor: pointer;
`;

function QuestionAnswers({ onAnswer, answerId }) {
    const { state } = React.useContext(QuizStore);
    const question = state.questions[state.currentQuestionIndex];

    // `onAnswer` is present when we want to display list of answers to click
    if (typeof onAnswer === 'function') {
        return (
            <React.Fragment>
                {question.answers.map(answer => (
                    <AnswerButton key={answer.id} onClick={() => onAnswer(question.id, answer.id)}>{answer.text}</AnswerButton>
                ))}
            </React.Fragment>
        );
    }

    // `answerId` is present when we want to display list of incorrect answers with `naswerId` marked red
    if (typeof answerId === 'number') {
        return (
            <React.Fragment>
                {question.answers.map(answer => (
                    <Answer key={answer.id} incorrect={answerId === answer.id}>{answer.text}</Answer>
                ))}
            </React.Fragment>
        );
    }

    return '?';
}

QuestionAnswers.propTypes = {
    answerId: PropTypes.number,
    onAnswer: PropTypes.func,
};

QuestionAnswers.defaultProps = {
    onAnswer: undefined,
    answerId: undefined,
};

export default QuestionAnswers;
