import React from 'react';
import PropTypes from 'prop-types';
import ReactModal from 'styled-react-modal';
import styled from 'styled-components';
import colorAlpha from 'color-alpha';

import ClientStore from '../../context/ClientStore/ClientStore';
import { toggleModal as toggleModalAction } from '../../context/ClientStore/ClientStoreActions';

const StyledModal = ReactModal.styled`
    align-items: center;
    background: ${({ theme }) => (colorAlpha(theme.contentTypes.color.darkishBlue, 0.8))};
    border-radius: 4px;
    box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.3);
    display: flex;
    height: 716px;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    width: 700px;
    overflow-y: auto;

    @media (max-width: 700px) {
        width: 100vw;
        height: 100vh;
    }
`;

const Image = styled.img`
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
`;

export const HEADER_HEIGHT = 54;

const ContentContainer = styled.div`
    box-sizing: border-box;
    flex: 1 1 auto;
    width: 100%;
`;

// Move to react-common and keep generic
function Modal({ children }) {
    const { state, dispatch } = React.useContext(ClientStore);

    const toggleModal = () => dispatch(toggleModalAction());

    return (
        <StyledModal
            isOpen={state.isOpen}
            // onBackgroundClick={toggleModal}
            onEscapeKeydown={toggleModal}
        >
            <Image src="https://bit.ly/2Kk5aaF" alt="temp bg image" />
            <ContentContainer>
                {children}
            </ContentContainer>
        </StyledModal>
    );
}

Modal.propTypes = {
    children: PropTypes.node.isRequired,
};

export default Modal;
