import { SET_ENVIRONMENT, TOGGLE_MODAL } from './ClientStoreActions';

const ClientReducer = (state, action = { type: '' }) => {
    switch (action.type) {
        case SET_ENVIRONMENT:
            state.environment = action.environment;
            return;
        case TOGGLE_MODAL:
            state.isOpen = !state.isOpen;
    }
};

export default ClientReducer;
